@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700&family=Tajawal:wght@200;300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:100,100italic,300,300italic,regular,italic,500,500italic,700,700italic,900,900italic);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@200;300;400;500;600;700&display=swap);
* {
  font-family: -apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans","Liberation Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
}

body{
  background-color: #F4F3F8 !important;
  font-size: 18px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar-brand {
  font-family: Merriweather !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-size: 20px !important;
  line-height: 28px !important;
  display: flex !important;
  align-items: center !important;

  color: #433939 !important;
}

.navbar {
  /* background: rgba(196, 196, 196, 0.09) !important; */
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.12) !important;
  border-radius: 10px !important;
  position: fixed !important;
  top: 0;
  width: 100% !important;
  z-index: 5;
}

.field {
  font-size: 18px;
  font-weight: 500;
  text-transform: capitalize;
}

.details {
  font-weight: normal;
  text-transform: capitalize;
  font-size: 18px;
}

.dimensions .field, .dimensions .details {
  font-size: 16px;
}
.active-page {
  z-index: 2;
  color: #0a58ca;
  background-color: #e9ecef;
  border-color: #dee2e6;
}
